:root {
  --moderate-cyan: hsl(176, 50%, 47%);
  --dark-cyan: hsl(176, 72%, 28%);
  --black: hsl(0, 0%, 0%);
  --dark-gray: hsl(0, 0%, 48%);
  --gray: hsl(0, 0%, 90%);
  --light-gray: hsl(0, 0%, 95%);
}

:root {
  --font-commissioner: "Commissioner", sans-serif;
  --font-bold: 700;
  --font-regular: 500;
  --font-light: 400;
}

body {
  font-family: var(--font-commissioner);
}

html {
  font-size: 100%;
  box-sizing: border-box;
}

*,
*::before,
*::after {
  box-sizing: inherit;
}

body {
  margin: 0;
  padding: 0;
  font-family: var(--font-commissioner);
}

h1,
h2,
h3 {
  margin-top: 0;
}

a,
a:visited,
a:active {
  text-decoration: none;
}

h2 {
  margin-bottom: 0;
}

p {
  opacity: 50%;
  font-weight: var(--font-regular);
  font-size: 1.1rem;
  letter-spacing: 0.05rem;
  line-height: 1.9rem;
}

button {
  border: none;
  background-color: var(--moderate-cyan);
  border-radius: 50px;
  padding: 20px 30px;
}

nav {
  height: 550px;
  background: url(../../../images/image-hero-desktop.jpg);
  background-size: cover;
  background-repeat: no-repeat;
  padding-left: 200px;
  padding-right: 200px;
  padding-top: 50px;
}
nav .nav__container {
  display: flex;
  justify-content: space-between;
}
nav .nav__container ul {
  display: flex;
  list-style: none;
}
nav .nav__container ul li a {
  color: white;
  margin-left: 2rem;
}

.starter {
  border: solid var(--gray) 1px;
  margin-bottom: 30px;
  border-radius: 10px;
  background: white;
  padding: 50px 60px;
  text-align: center;
}
.starter .logo {
  position: absolute;
  top: -28px;
  left: 0;
  right: 0;
  margin-left: auto;
  margin-right: auto;
}
.starter h2 {
  font-size: 2.25rem;
  color: var(--black);
}
.starter p {
  margin-bottom: 40px;
}
.starter div {
  display: flex;
  justify-content: space-between;
}
.starter .button {
  height: 56px;
  font-weight: var(--font-bold);
  font-size: 1.1rem;
  letter-spacing: 0.05rem;
  color: white;
}
.starter .bookmark {
  height: 56px;
  padding-left: 0;
  padding-right: 25px;
  text-align: center;
  background-color: var(--gray);
  display: flex;
  align-items: center;
  font-weight: var(--font-bold);
  font-size: 1.1rem;
  color: var(--dark-gray);
}
.starter .bookmark img {
  margin-right: 10px;
}

.stats {
  border: solid var(--gray) 1px;
  margin-bottom: 30px;
  border-radius: 10px;
  background: white;
  padding: 50px 60px;
}

.about {
  border: solid var(--gray) 1px;
  margin-bottom: 30px;
  border-radius: 10px;
  background: white;
  padding: 50px 60px;
}

body {
  background-color: var(--light-gray);
}

main {
  position: absolute;
  top: 425px;
  left: 0;
  right: 0;
  margin-left: auto;
  margin-right: auto;
  max-width: 925px;
}